import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class BeerService {

    constructor (
        private http: Http
    ) {}

    getUser() {
        return this.http.get(`/randombeer`)
            .map((res:Response) => res.json());
    }

    someMethod() {
        return 'Hey!';
    }

}


